import { React, useState } from "react";
import shops from "../State/State";
import { FaPlusCircle } from "react-icons/fa";
import AddItems from "./AddItems";
import MultipleShops from "./MultipleShops";

const SelectShop = () => {
  const [selectShop, setSelectShop] = useState(false);
  const [saveShopChoice, setSaveShopChoice] = useState([]);

  const saveShopChoiceFunction = (name, image, id) => {
    setSaveShopChoice([...saveShopChoice, { name, image, id }]);
    setSelectShop(!selectShop);
  };

  return (
    <div>
      <div className="container">
        <p className="add-shop">Select a shop </p>
        <FaPlusCircle
          className="add add-shop"
          onClick={() => setSelectShop(!selectShop)}
        />
      </div>
      <div
        className={`shop-container ${
          selectShop ? "show-shop-container" : "hide-shop-container"
        }`}
      >
        {shops.map((shop) => {
          const { id, name, image } = shop;
          return (
            <>
              <img
                className="shop-image"
                key={id}
                src={image}
                alt={`select ${name}`}
                onClick={() => saveShopChoiceFunction(name, image, id)}
              />
            </>
          );
        })}
      </div>
      {/* add map functionality to return the add items for each shop selected */}
      <AddItems saveShop={saveShopChoice} />
      {/* <MultipleShops saveShop={saveShop} setSaveShop={setSaveShop} /> */}
    </div>
  );
};

export default SelectShop;
