import { React, useState } from "react";
import List from "./List";
import { v4 as uuidv4 } from "uuid";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { FaTrash, FaGripLines } from "react-icons/fa";

const AddItems = ({ saveShop }) => {
  const [item, setItem] = useState("");
  const [list, setList] = useState([]);
  // const [message, setMessage] = useState({});

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!item) {
      return;
    } else {
      setList([...list, { item, id: uuidv4() }]); //npm package that generates random ID's
      setItem("");
    }
  };

  const clearItem = (id) => {
    setList(list.filter((list) => list.id !== id)); // return all ID's not equal to ID
  };

  const handleOnDragEnd = (result) => {
    if (!result.destination) return; //handles moving the list item outside of list e.g. to right or left of screen

    //console.log(result); //gets the destination and source of the item position
    const items = Array.from(list);
    const [reorderedItem] = items.splice(result.source.index, 1); //remove moved item from array
    //console.log(reorderedItem);
    items.splice(result.destination.index, 0, reorderedItem); //put removed item back in destintation position

    setList(items); //update state after change
  };

  return (
    <div>
      {saveShop.length > 0 && (
        <img
          className="selected-shop-image"
          src={saveShop[0].image}
          alt={`${saveShop[0].name}`}
        />
      )}
      <div className="input-container">
        <input
          className="list-input"
          type="text"
          placeholder="Add to list"
          onChange={(e) => setItem(e.target.value)}
          value={item} // this sets the e.target back to state
        />
        <button className="add-btn" onClick={(e) => handleSubmit(e)}>
          Add
        </button>
      </div>
      <DragDropContext onDragEnd={handleOnDragEnd}>
        <Droppable droppableId="listItem">
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef}>
              {list.map(
                (listItem, index) => {
                  return (
                    <Draggable
                      key={listItem.id}
                      index={index}
                      draggableId={listItem.id}
                    >
                      {(provided) => (
                        <div
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          ref={provided.innerRef}
                        >
                          <div className="list-container">
                            <FaGripLines className="react-icons" />
                            <div className="list-items">{listItem.item}</div>
                            <FaTrash
                              className="react-icons"
                              onClick={() => clearItem(listItem.id)}
                            />
                          </div>
                        </div>
                      )}
                    </Draggable>
                  );
                }

                /* {list.map((listItem, index) => (
        <List
          key={listItem.id}
          id={listItem.id}
          list={listItem.item}
          clearItem={clearItem}
          index={index}
        />
      ))} */
              )}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};

export default AddItems;
